.Movie {

  display: grid;
  /*grid-template-columns: repeat(3, minmax(max-content, 1fr));*/
  grid-template-columns: repeat(8, 12.5%);
  grid-template-rows: repeat(6, 40px);

  margin: 20px;

}

.Movie_Columns.Poster {
  grid-row: 1 / span 6;
  grid-column: 1 / span 3;
  z-index: 1;
}

img {
  max-width: 100%;
}

.Movie_Columns.Article {
  font-size: 70%;
  background-color: #f5f6fa;
  grid-row: 2 / span 5;
  grid-column: 2 / span 7;
  z-index: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-left: 33.4%;/*1/7*2 + 1/7/3 -> 28.6%+4.8%=33.4%*/
  padding-right: 4.8%;/*1/7/3 -> 4.8%*/
  box-shadow: 3px 3px 7px #636e72;
}

@media screen and (min-width: 600px) {
  .Movie_Columns.Article {
    padding-left: 145px;
    padding-right: 55px;
    font-size: 85%;
  }
  h1 {
    font-size: 160%;
  }

}

@media screen and (max-width: 999px) {
  img {
    max-height: 100%;
  }
}

@media screen and (min-width: 1000px) {
  .Movie_Columns.Article {
    padding-left: 33.4%;
    padding-right: 4.8%;
    font-size: 70%;
  }
  img {
    max-height: 100%;
  }
}

@media screen and (min-width: 1400px) {
  .Movie_Columns.Article {
    padding-left: 145px;
    padding-right: 55px;
    font-size: 85%;
  }
  h1 {
    font-size: 160%;
  }

}
